import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import * as moment from 'moment';
import { HandleInvoiceService } from '@modules/handle-invoice/shared';
import { Library} from '@core/index';
@Component({
  selector: 'app-choose-hdck',
  templateUrl: './choose-hdck.component.html',
  styleUrls: ['./choose-hdck.component.scss']
})
export class ChooseHdckComponent implements OnInit {

  constructor(
    public bsModalRef: BsModalRef,
    private handleInvoiceService: HandleInvoiceService,
  ) { }

  selectedRows: Array<any> = [];

  invoiceList: any;
  invoiceNumberOrigin: any;
  invoiceReleaseId: any;
  invoiceDateOrigin: any;
  onSave: any;

  ngOnInit() {
    // console.log(this.invoiceNumberOrigin);
    // console.log(this.templateCode);
    Library.showloading();
    this.handleInvoiceService.layCacHoaDonChoKy(this.invoiceDateOrigin, this.invoiceReleaseId).subscribe((rs: any) => {
      this.invoiceList = rs.data.invoices;
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  choose() {
    this.onSave(this.selectedRows[0]);
    this.bsModalRef.hide();
  }

}
