import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { Subscription } from 'rxjs';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { AddCustomerComponent } from '@shared/components';
import { AuthService } from '@core/services';
import { ActivatedRoute } from '@angular/router';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import * as moment from 'moment';
import { pattern } from '@config/index';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { sEnum } from '@config/sEnum';
import DataSource from 'devextreme/data/data_source';
import { VChanggeMoneyService } from '@core/library/invoice/v-changge-money.service';
import { VStringService } from '@core/library/public/v-string.service';
import { Helper } from '@core/helper/Helper';
import { Library } from '@core/library';
import { Router, NavigationExtras } from '@angular/router';
import { ChooseHdckComponent } from '@modules/handle-invoice-student/components/choose-hdck/choose-hdck.component';

@Component({
  selector: 'app-adjust-form',
  templateUrl: './adjust-form.component.html',
  styleUrls: ['./adjust-form.component.scss']
})
export class AdjustFormComponent implements OnInit {

  routeSub: Subscription;
  @ViewChildren(DxFormComponent) listDxForm: any;
  @ViewChild(DxFormComponent) form: DxFormComponent;

  @Input() frmData: any;
  @Input() TLMG_student: any;
  @Input() dataSource: any;
  @Input() oldForm: any;
  @Input() type: any;
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() _objTemplateSelect: any;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Output('adjustmentObjectChange') adjustmentObject = new EventEmitter<any>();
  @Output('onTemplateTypeChange') onTemplateTypeChange = new EventEmitter<any>();
  @Output('onTemplateTT78TypeChange') onTemplateTT78TypeChange = new EventEmitter<any>();
  @Input() infoDangKy: any;
  @Input() isTT78: boolean;
  @Input() _oTemplateTT78: any;
  @Input() fileData: any;

  bsModalRef: BsModalRef;
  bsModalHdckRef: BsModalRef;

  emails: any = [];
  private arrEmail: any = [];

  select: any = {};
  pattern = pattern;
  tax_codes: any = [];
  customer_codes: any = [];
  readOnlyCacTruongConLai: boolean;
  minNgayVB: string;
  maxNgayVB: string;
  customerCurent: any = {};
  showPopup = false;
  disableForm = {
    tax_code: true,
    rest: true
  };
  formItems = [];
  customForms = [];
  customerTempt = {};
  // thay i disable ca 3 input customer_name, buyer_name, buyer_address
  tod: any = {};
  disableInvoiceDate = this.authService.isAllowEditInvoiceDate();
  htXuatOptions = [];
  _translate: any;
  configs: any;
  origin_btn_options = {
    text: '', icon: 'fa fa-eye', type: 'default',
    onClick: () => {
      let ids = [];
      ids.push(this.oldForm.id);

      if (this.oldForm.adjustment_type === 5) {
        if (this.oldForm.template_code.indexOf('02GTTT') >= 0)
          window.open(`/system/handle-invoice-student/edit-adjust-sale/${this.oldForm.id}`, '_blank');
        else
          window.open(`/system/handle-invoice-student/edit-adjust-vat/${this.oldForm.id}`, '_blank');
      } else if (this.oldForm.adjustment_type === 3) {
        if (this.oldForm.template_code.indexOf('02GTTT') >= 0)
          window.open(`/system/handle-invoice-student/edit-replace-sale/${this.oldForm.id}`, '_blank');
        else
          window.open(`/system/handle-invoice-student/edit-replace-vat/${this.oldForm.id}`, '_blank');
      } else {
        if (this.oldForm.template_code.indexOf('02GTTT') >= 0) {
          window.open(`/system/student-sale-invoice/${this.oldForm.id}`, '_blank');
        } else {
          window.open(`/system/student-vat-invoice/${this.oldForm.id}`, '_blank');
        }
      }

    }
  };

  mst_btn_options = {
    text: '', icon: 'assets/img/icons/Them.png', type: 'default',
    onClick: () => {
      const initialState = {
        isVisibleGhiVaThemMoi: false
      };
      this.bsModalRef = this.modalService.show(AddCustomerComponent, {
        class: 'modal-lg full-modal', initialState, backdrop: 'static',
        keyboard: false
      });
      const submodal = this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveData) {
          const customer = this.bsModalRef.content.successSaveData;
          this.frmData.customer_name = customer.name;
          this.frmData.buyer_tax_code = customer.tax_code;
          this.frmData.buyer_name = customer.contact_person_name;
          this.frmData.buyer_address = customer.business_address;
          this.frmData.customer_code = customer.code;
          this.frmData.buyer_mobile = customer.contact_mobile;
          if (customer.email) {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
            const stringDataEmailGet = customer.email.split(';');
            for (let i = 0; i < stringDataEmailGet.length; i++) {
              this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
            }
          } else {
            this.frmData.buyer_email = customer.email;
            this.emails = [];
          }
          if (customer.bank_account_number) {
            this.frmData.bank_account_number = customer.bank_account_number;
          }
          if (customer.bank_name) {
            this.frmData.bank_name = customer.bank_name;
          }
        }
        submodal.unsubscribe();
      });
    }
  };

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    private vatService: VatService,
    public translate: TranslateService,
    private customersService: CustomersService,
    private router: Router,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());

  }

  ngOnInit() {
    this.minNgayVB = moment(this.oldForm.invoice_date).format('YYYY-MM-DD');
    this.maxNgayVB = moment(new Date()).format('YYYY-MM-DD');
    this.routeSub = this.route.data.subscribe((data: any) => {

      this.configs = this.authService.getConfigs();
      // put data DM for select
      this.select.unit_codes = data.init[1].DM_DON_VI_TINH;
      this.select.payment_method_names = data.init[1].DM_HINH_THUC_THANH_TOAN;
      this.select.currency_codes = data.init[1].DM_LOAI_TIEN;
      this.select.customer_types = data.init[1].DM_NHOM_KHACH_HANG;
      this.select.export_places = data.init[1].DM_NOI_XUAT_HOA_DON;
      this.select.adjustment_forms = VSelectBoxService._adjustmentForms(this._translate);
      this.htXuatOptions = VSelectBoxService._htXuatOptions(this._translate, this.frmData.status);
      if (this.configs.system_parameter.allow_keep_invoice_number === false) {
        this.htXuatOptions = this.htXuatOptions.filter(e => e.code !== sEnum.hinhThucXuat.XuatChoKy);
      }
      if (this.isTT78) {
        let temptTT78 = [];

        temptTT78 = data.init[0].filter(item => item.template_type !== 'HOA_DON_KHOI_TAO_TU_MAY_TINH_TIEN'&& item.template_type !== 'PHIEU_THU_MAY_TINH_TIEN');
        this.select.adjustment_forms = this.select.adjustment_forms.filter(p => p.code != 'TANG_GIAM');
        if (temptTT78 && temptTT78.length > 0) {
          this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.infoDangKy.is_invoice_with_code);
          if (Number.parseInt(this.frmData.invoice_number) > 0) {
            if (this.frmData.is_invoice_with_code != this.infoDangKy.is_invoice_with_code) {
              this.select.templatesTT78 = temptTT78.filter(p => p.is_invoice_with_code == this.frmData.is_invoice_with_code);
            }
          }
        }
        if (this.frmData.id)
          this.changeTemplateInvoice(this.frmData.invoice_date);
      }
      else {
        this.select.invoice_releases = data.init[0];
      }
    });
    this.select.customer_object_codes = VSelectBoxService._customerObject(this._translate);
    this.select.adjustment_objects = [];
    if (this.frmData.adjustment_form) {
      this.adjustmentFormChange({ value: this.frmData.adjustment_form });
    }
    // Set disabled cho n v t chc
    this.setDisabledByCustomer_object_code(this.frmData.customer_object_code);

    // tm kim m s thu
    this.tax_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'tax_code');
      }
    });

    // // tm kim m khch hng
    this.customer_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'code');
      }
    });
    if (!this.isTT78) {
      this.select.invoice_releases.forEach(item => {
        if (item.template_id === this.frmData.template_id) {
          // Nu l mu html
          if (item.fields && item.fields.length > 0) {
            this.formItems = [];
            this.customForms = [];
            const _dulieumorong = item.fields.filter(x => x.type === '4');
            if (_dulieumorong.length > 0) {
              _dulieumorong.forEach(item => {
                if (item.is_show) {
                  if (item.data_type === 'chu') {
                    this.customForms.push(
                      {
                        editorType: 'dxTextBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }
                        }
                      }
                    );
                  }
                  if (item.data_type === 'soluong') {
                    this.customForms.push(
                      {
                        editorType: 'dxNumberBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }
                        }
                      }
                    );
                  }
                  if (item.data_type === 'ngay') {
                    this.customForms.push(
                      {
                        editorType: 'dxDateBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }, displayFormat: 'dd/MM/yyyy', dateSerializationFormat: 'yyyy-MM-dd HH:mm:ss',
                        }
                      }
                    );
                  }
                  if (item.data_type === 'danhmuc') {
                    this.customForms.push(
                      {
                        editorType: 'dxSelectBox',
                        dataField: item.code,
                        label: { text: item.name }
                      }
                    );
                  }
                }
              });
              this.formItems.push(this.createCustomGroup());
            }
            if (this.frmData.invoice_options && this.frmData.invoice_options.length > 0) {
              this.frmData.invoice_options.forEach(iteminv => {
                this.customerTempt[iteminv.code] = iteminv.value;
              });
            } else {
              _dulieumorong.forEach(item => {
                this.customerTempt[item.code] = item.value;
              });
            }
          }
        }
      });
    }
    else {
      this.select.templatesTT78.forEach(item => {
        if (item.value === this.frmData.template_id) {
          // Nu l mu html
          if (item.fields && item.fields.length > 0) {
            this.formItems = [];
            this.customForms = [];
            const _dulieumorong = item.fields.filter(x => x.type === '4');
            if (_dulieumorong.length > 0) {
              _dulieumorong.forEach(item => {
                if (item.is_show) {
                  if (item.data_type === 'chu') {
                    this.customForms.push(
                      {
                        editorType: 'dxTextBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }
                        }
                      }
                    );
                  }
                  if (item.data_type === 'soluong') {
                    this.customForms.push(
                      {
                        editorType: 'dxNumberBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }
                        }
                      }
                    );
                  }
                  if (item.data_type === 'ngay') {
                    this.customForms.push(
                      {
                        editorType: 'dxDateBox',
                        dataField: item.code,
                        label: { text: item.name },
                        editorOptions: {
                          onValueChanged: () => {
                            this.changeItem(_dulieumorong);
                          }, displayFormat: 'dd/MM/yyyy', dateSerializationFormat: 'yyyy-MM-dd HH:mm:ss',
                        }
                      }
                    );
                  }
                  if (item.data_type === 'danhmuc') {
                    this.customForms.push(
                      {
                        editorType: 'dxSelectBox',
                        dataField: item.code,
                        label: { text: item.name }
                      }
                    );
                  }
                }
              });
              this.formItems.push(this.createCustomGroup());
            }
            if (this.frmData.invoice_options && this.frmData.invoice_options.length > 0) {
              this.frmData.invoice_options.forEach(iteminv => {
                this.customerTempt[iteminv.code] = iteminv.value;
              });
            } else {
              _dulieumorong.forEach(item => {
                this.customerTempt[item.code] = item.value;
              });
              this.frmData.invoice_options = _dulieumorong;
            }
          }
        }
      });
    }

    if (this.frmData.buyer_email) {
      this.emails = [];
      const emailToString = this.frmData.buyer_email;
      const a11 = emailToString.replace(/;/g, ',');
      this.emails = a11.split(',');
    }
  }

  ngAfterViewInit() {
    // this.listDxForm._results[1].readOnly = true;
  }

  readonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = true;
    });
    this.readOnlyCacTruongConLai = true;
  }

  public removeReadonlyAllForm() {
    this.listDxForm._results.forEach(item => {
      item.readOnly = false;
      // item.instance.resetValues();
    });
    this.readOnlyCacTruongConLai = false;
  }

  ////// To form m rng
  createCustomGroup() {
    return {
      itemType: 'group',
      colCount: '2',
      items: this.customForms
    };
  }

  /////// s kin thay i cc trng m rng
  changeItem(_dulieumorong) {
    _dulieumorong.forEach(item => {
      for (const key in this.customerTempt) {
        if (item.code === key) {
          item.value = this.customerTempt[key];
        }
      }
      this.frmData.invoice_options = _dulieumorong;
    });
  }

  setDisabledByCustomer_object_code(value) {
    this.tod.customer_name = false;
    this.tod.buyer_name = false;
    this.tod.buyer_address = false;
    switch (value) {
      case 'DOANH_NGHIEP':
        break;
      case 'TRUONG_HOC':
        this.frmData.customer_name = '';
        this.tod.customer_name = true;
        break;
      default:
        break;
    }
  }

  getHtXuatValue() {
    return this.frmData.status;
  }

  // gi data ln parent component
  onFieldDataChanged() {
    if (this.frmData.adjustment_object === 'MST') {
      this.dataSource.items()[0].product_name = `iu chnh m s thu Cng ty/n v mua hng t ${this.oldForm.buyer_tax_code} thnh ${this.frmData.buyer_tax_code}`;
      this.dataSource.reload();
    }
    if (this.frmData.adjustment_object === 'KH_DC') {
      let _noiDungDc = '';
      if (this.frmData.student_name && this.oldForm.student_name !== this.frmData.student_name) {
        _noiDungDc = `iu chnh tn hc sinh t ${this.oldForm.student_name} thnh ${this.frmData.student_name}`;
      }
      if (this.frmData.buyer_address && this.oldForm.buyer_address !== this.frmData.buyer_address) {
        if (_noiDungDc) {
          _noiDungDc += '\n';
        }
        _noiDungDc += `iu chnh a ch ca n v mua hng t ${this.oldForm.buyer_address} thnh ${this.frmData.buyer_address}`;
      }
      this.dataSource.items()[0].product_name = _noiDungDc;
      this.dataSource.reload();
    }
    this.formEvent.emit(this.frmData);
  }

  adjustmentFormChange = (e) => {
    switch (e.value) {
      case 'THONG_TIN':
        this.select.adjustment_objects = [
          {
            name: 'Tin ch',
            code: 'TIEN_CHU'
          },
          {
            name: 'Tn hc sinh, a ch, ...',
            code: 'KH_DC'
          },
        ];
        break;
      case 'TANG':
        this.select.adjustment_objects = [
          {
            name: 'Hng ha dch v',
            code: 'HH_DV'
          },
          {
            name: 'Khc',
            code: 'KHAC_TANG'
          },
        ];
        break;
      case 'GIAM':
        this.select.adjustment_objects = [
          {
            name: 'Hng ha dch v',
            code: 'HH_DV'
          },
          {
            name: 'Khc',
            code: 'KHAC_GIAM'
          },
        ];
        break;
      case 'KHAC':
        this.select.adjustment_objects = [
          {
            name: 'Khc',
            code: 'KHAC'
          },
        ];
        this.frmData.adjustment_object = 'KHAC';
        break;
      default:
        break;
    }
  };

  adjustmentObjectChange = (e) => {
    if (this.frmData.adjustment_form === 'THONG_TIN' && e.value === 'KH_DC') {
      this.disableForm = { tax_code: false, rest: false };
      this.adjustmentObject.emit('THONG_TIN_KH_DC');
    } else if (this.frmData.adjustment_form === 'THONG_TIN' && e.value === 'TIEN_CHU') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('THONG_TIN_TIEN_CHU');
    } else if (this.frmData.adjustment_form === 'KHAC' && e.value === 'KHAC') {
      this.disableForm = { tax_code: false, rest: false };
      this.adjustmentObject.emit('KHAC_KHAC');
    } else if (this.frmData.adjustment_form === 'TANG' && e.value === 'HH_DV') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('TANG_HH_DV');
    } else if (this.frmData.adjustment_form === 'TANG' && e.value === 'KHAC_TANG') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('TANG_KHAC_TANG');
    } else if (this.frmData.adjustment_form === 'GIAM' && e.value === 'HH_DV') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('GIAM_HH_DV');
    } else if (this.frmData.adjustment_form === 'GIAM' && e.value === 'KHAC_GIAM') {
      this.disableForm = { tax_code: true, rest: true };
      this.adjustmentObject.emit('GIAM_KHAC_GIAM');
    }
    if (e.value) {
      this.frmData.buyer_tax_code = this.oldForm.buyer_tax_code;
      this.frmData.customer_name = this.oldForm.customer_name;
      this.frmData.buyer_address = this.oldForm.buyer_address;
    }
    // this.adjustmentObject.emit(e.value);
  };

  taxCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.customer_code = customer.code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    this.onFieldDataChanged();
  }

  customerCodeChoose(e) {
    const customer = e.itemData;
    if (this.frmData.customer_object_code !== 'CA_NHAN' && this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.customer_name = customer.name;
    }
    this.frmData.buyer_name = customer.contact_person_name;
    if (this.frmData.customer_object_code !== 'KHACH_LE') {
      this.frmData.buyer_address = customer.business_address;
    }
    this.frmData.buyer_tax_code = customer.tax_code;
    this.frmData.buyer_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.buyer_email = customer.email;
      this.emails = [];
    }
    this.frmData.customer_id = customer.id;
    if (customer.bank_account_number) {
      this.frmData.bank_account_number = customer.bank_account_number;
    }
    if (customer.bank_name) {
      this.frmData.bank_name = customer.bank_name;
    }

    this.onFieldDataChanged();
  }

  customer_object_codeChange = (e) => {
    this.setDisabledByCustomer_object_code(e.value);
  };

  templateChange = (e) => {
    this.select.invoice_releases.forEach(item => {
      if (e.value === item.value) {
        this.frmData.template_id = item.template_id;
        this._objTemplateSelect = item;
        this.onTemplateTypeChange.emit(item);
        if (!this._objTemplateSelect.is_discount) {
          this.dataSource.items().forEach(item => {
            item.discount = null;
            item.amount_discount = null;
          });
          VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._objTemplateSelect, this.configs.system_parameter);
          if (VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBay
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBayHoanTien
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonVeMayBayThuTien
            || VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonXangDau) {
            VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._objTemplateSelect.template_type));
          }
        }
        // Nu l mu html
        if (item.fields) {
          this.formItems = [];
          this.customForms = [];
          const _dulieumorong = item.fields.filter(x => x.type === '4');
          if (_dulieumorong.length > 0) {
            _dulieumorong.forEach(item => {
              if (item.is_show) {
                if (item.data_type === 'chu') {
                  this.customForms.push(
                    {
                      editorType: 'dxTextBox',
                      dataField: item.code,
                      label: { text: item.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, disabled: true
                      }
                    }
                  );
                }
                if (item.data_type === 'soluong') {
                  this.customForms.push(
                    {
                      editorType: 'dxNumberBox',
                      dataField: item.code,
                      label: { text: item.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, disabled: true
                      }
                    }
                  );
                }
                if (item.data_type === 'soluong') {
                  this.customForms.push(
                    {
                      editorType: 'dxNumberBox',
                      dataField: item.code,
                      label: { text: item.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, disabled: true
                      }
                    }
                  );
                }
                if (item.data_type === 'ngay') {
                  this.customForms.push(
                    {
                      editorType: 'dxDateBox',
                      dataField: item.code,
                      label: { text: item.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, displayFormat: 'dd/MM/yyyy', dateSerializationFormat: 'yyyy-MM-dd HH:mm:ss', disabled: true
                      }
                    }
                  );
                }
                if (item.data_type === 'danhmuc') {
                  this.customForms.push(
                    {
                      editorType: 'dxSelectBox',
                      dataField: item.code,
                      label: { text: item.name }
                    }
                  );
                }
              }
            });
            this.formItems.push(this.createCustomGroup());
          }
          _dulieumorong.forEach(item => {
            this.customerTempt[item.code] = item.value;
          });

        } else {
          this.formItems = [];
          this.customerTempt = {};
        }
      }
    });
  };

  onChangeTem = (e) => {
    this.select.templatesTT78.forEach(item => {
      if (e.value === item.value) {
        this.frmData.template_id = item.value;
        this.frmData.invoice_series = item.invoice_series;
        this.frmData.template_code = item.template_code;
        this.onTemplateTT78TypeChange.emit(item);
        for (const key in item) {
          if (item.hasOwnProperty(key)) {
            const value = item[key];
            this._oTemplateTT78[key] = value;
          }
        }
        if (!this._oTemplateTT78.is_discount) {
          this.invoice_products.load().then((dataSource) => {
            dataSource.forEach((item, index) => {
              item.discount = null;
              item.amount_discount = null;
            });
          });
          VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._oTemplateTT78, this.configs.system_parameter);
          if (VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBay
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBayThuTien
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonVeMayBayHoanTien
            || VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonXangDau) {
            VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._oTemplateTT78.template_type));
          }
        }

        // Nu l mu html
        if (item.fields) {
          this.formItems = [];
          this.customForms = [];
          const _arrayGrid = item.fields.filter(x => x.type === '5');
          const _dulieumorong = item.fields.filter(x => x.type === '4');
          if (_dulieumorong.length > 0) {
            _dulieumorong.forEach(item1 => {
              if (item1.is_show) {
                if (item1.data_type === 'chu') {
                  this.customForms.push(
                    {
                      //  cssClass: 'vat layout_input',
                      editorType: 'dxTextBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }
                      }
                    }
                  );
                }
                if (item1.data_type === 'soluong') {
                  this.customForms.push(
                    {
                      //    cssClass: 'vat layout_input',
                      editorType: 'dxNumberBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }
                      }
                    }
                  );
                }
                if (item1.data_type === 'ngay') {
                  this.customForms.push(
                    {
                      //   cssClass: 'vat layout_input',
                      editorType: 'dxDateBox',
                      dataField: item1.code,
                      label: { text: item1.name },
                      editorOptions: {
                        onValueChanged: () => {
                          this.changeItem(_dulieumorong);
                        }, displayFormat: 'dd/MM/yyyy', dateSerializationFormat: 'yyyy-MM-dd HH:mm:ss',
                      }
                    }
                  );
                }
                if (item1.data_type === 'danhmuc') {
                  this.customForms.push(
                    {
                      //    cssClass: 'vat layout_input',
                      editorType: 'dxSelectBox',
                      dataField: item1.code,
                      label: { text: item1.name }
                    }
                  );
                }
              }
            });
            this.formItems.push(this.createCustomGroup());
            this.frmData.invoice_options = _dulieumorong;
          }
          else
            this.frmData.invoice_options = [];
          _dulieumorong.forEach(value => {
            this.customerTempt[value.code] = value.value;
          });
        } else {
          this.formItems = [];
          this.customerTempt = {};
        }
      }
    });
  };

  currencyCodeChange = (e) => {
    if (this.frmData.currency_code === 'VND') {
      this.frmData.currency_rate = null;
    }
    this.frmData.total_payment_in_word = Helper.tienBangChu(this.frmData.total_payment, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), this.frmData.currency_code, this.configs.system_parameter.display_read_money_english);
  };
  exportPlaceChange = (e) => {
    this.select.export_places.forEach(item => {
      if (e.value === item.code) {
        this.frmData.export_address = item.dia_chi;
      }
    });
  };
  currencyRateChange = (e) => {
    if (!this.isTT78)
      VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._objTemplateSelect, this.configs.system_parameter);
    else
      VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, null, this._oTemplateTT78, this.configs.system_parameter);
  };

  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }

  xuatMauBienBan() {
    Library.showloading();
    const inv = Object.assign({}, this.oldForm);
    inv.doc_relate_date = this.frmData.doc_relate_date;
    inv.doc_relate_symbol = this.frmData.doc_relate_symbol;
    inv.destroy_reason = this.frmData.destroy_reason;

    inv.invoice_files = [];
    this.vatService.xuatMauBienBanDc(inv).subscribe(rs => {
      if (rs.status === 'success') {
        const file = rs.data.invoice;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
    });
  }

  showModalHdck() {
    const initialState = {
      invoiceNumberOrigin: this.oldForm.invoice_number,
      templateCode: this.oldForm.template_code,
      invoiceDateOrigin: this.oldForm.invoice_date,
      onSave: (data) => {
        this.frmData.id = data.id;
        this.frmData.invoice_date = data.invoice_date;
        this.frmData.invoice_number = data.invoice_number;
        this.frmData.batch_id = data.batch_id;
      }
    };
    this.bsModalHdckRef = this.modalService.show(ChooseHdckComponent, {
      backdrop: 'static',
      keyboard: false,
      class: 'modal-lg full-modal',
      initialState
    });
  }

  renderDataEmail($event) {
    this.arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          this.arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            this.arrEmail.push(this.emails[i]);
      }
      const emailToString = this.arrEmail.toString();
      this.frmData.buyer_email = emailToString.replace(/,/g, ';');
    } else {
      this.frmData.buyer_email = null;
    }
    this.formEvent.emit(this.frmData);
  }

  huy() {
    this.showPopup = false;
  }

  changeDateTime = (e) => {
    this.changeTemplateInvoice(e.value);
  }

  changeTemplateInvoice(strDate) {
    if (this.isTT78) {
      let year = "";
      if (VStringService._getValueToString(strDate) != "")
        year = VStringService._getValueToString(strDate).substring(4, 2);
      else {
        let invoiceDate = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
        year = VStringService._getValueToString(invoiceDate).substring(4, 2);
      }
      let templates = this.select.templatesTT78;
      this.select.templatesTT78 = [];
      if (templates && templates.length > 0) {
        templates.forEach(item => {
          if (VStringService._getValueToString(item.name) != "")
            item.name = item.name.replace(item.name.substring(4, 2), year);
          // for (const key in item) {
          //   if (item.hasOwnProperty(key)) {
          //     const value = item[key];
          //     this._oTemplateTT78[key] = value;
          //   }
          // }
          if (item.value === this.frmData.template_id) {
            this.onTemplateTT78TypeChange.emit(item);
          //  item.invoice_series = item.invoice_series.replace(item.invoice_series.substring(3, 1), year);
           // this.frmData.invoice_series = item.invoice_series;
           // this.frmData.template_code = item.template_code;
          }
          this.select.templatesTT78.push(item);
        });
      }
    }
  }
  disableTemplateInput() {
    if (this.frmData.invoice_number) {
      return true;
    }
    return false;
  }
}
