import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild, ViewChildren } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { DxDataGridComponent, DxFormComponent } from 'devextreme-angular';
import CustomStore from 'devextreme/data/custom_store';

import { AuthService, Helper, VChanggeMoneyService, VStringService } from '@core/index';
import { ConfigDecimalComponent } from '@shared/index';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { sEnum } from '@config/sEnum';
import * as moment from 'moment';
import { SelectMonthComponent } from '@shared/components/select-month/select-month.component';

@Component({
  selector: 'app-adjust-grip',
  templateUrl: './adjust-grip.component.html',
  styleUrls: ['./adjust-grip.component.scss']
})
export class AdjustGripComponent implements OnInit, OnDestroy {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @ViewChildren(DxFormComponent) listDxForm: any;

  routeSub: Subscription;

  contextMenu = [{ text: 'Thm' }, { text: 'Xa' }];
  @Input() dataSource: any;
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() not_auto: any;
  @Input() oldForm: any;
  @Input() _objTemplateSelect: any;
  @Input() isTT78: boolean;
  @Input() _oTemplateTT78: any;
  select: any = {};
  @Input() frmData: any;
  fee_months: any;
  bsModalRef: BsModalRef;
  showPopup = false;
  dataExcelFile: any = null;
  ttDaXuatHoaDon = false;
  isTextProductName = false;
  gripAllowEdit = false;
  configs: any;
  isHdGTGT = true;
  _translate: any;
  selectMonth = false;
  @Output('gripChange') gripChange = new EventEmitter<Array<any>>();
  @Output('autoChange') autoChange = new EventEmitter<any>();


  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private router: Router,
    private modalService: BsModalService,
    private vatService: VatService,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());

  }

  ngOnInit() {
    if (this.oldForm.template_code.indexOf('02GTTT') >= 0 || this.oldForm.template_code == "2") {
      this.isHdGTGT = false;
    }
    this.configs = this.authService.getConfigs();
    const currentUser = this.authService.getCurrentUser();
    const moth: any = moment();
    this.select.arrayYears = Helper.listYears(currentUser.account.start_time_study, moth);
    this.fee_months = this.select.arrayYears[1];
    this.routeSub = this.route.data.subscribe((data: any) => {
      this.select.unit_codes = data.init[1].DM_DON_VI_TINH;
      this.select.vats = data.init[1].DM_TY_LE_VAT;
      this.select.select_provisions_on_accounts = data.init[1].DM_DINH_KHOAN;
      this.select.products = [];
      this.oldForm.invoice_products.forEach(item => {
        this.select.products.push(item);
      });
      this.select.productsView = {
        store: new CustomStore({
          key: 'product_name',
          loadMode: 'raw',
          load: (loadOptions) => {
            return this.select.products;
          },
          byKey: function (key) {
            return [];
          }
        }),
        paginate: true,
        sort: 'product_name'
      };
    });

    //Event
    this.createUpdateEvent();
  }

  lookupColumnOptions = {
    acceptCustomValue: true,
    showClearButton: true,
    onCustomItemCreating: function (args) {
      const newItem: any = {};
      newItem.code = VStringService._convertToViKdau(args.text);
      newItem.name = args.text;
      this.select.unit_codes.unshift(newItem);
      setTimeout(() => {
        this.dataGrid.instance.columnOption('unit_code', 'lookup', {
          dataSource: this.select.unit_codes,
          displayExpr: 'name',
          valueExpr: 'code'
        });
      });

      return newItem;
    }.bind(this)
  };

  aa() {
    this.isTextProductName = !this.isTextProductName;
    // this.dataGrid.instance.repaint()
    setTimeout(() => {
      this.dataGrid.instance.repaint();
      this.dataGrid.instance.refresh();
    }, 100);
  }

  changeColumnProductNameToSelect() {
    this.isTextProductName = true;
    setTimeout(() => {
      this.dataGrid.instance.refresh();
    }, 100);
  }

  changeColumnProductNameToText() {
    this.isTextProductName = false;
    setTimeout(() => {
      this.dataGrid.instance.refresh();
    }, 100);
  }

  // khi to s kin khi update
  createUpdateEvent() {
    setTimeout(() => {
      this.invoice_products.on('updated', (key, value) => {
        const column = Object.keys(value)[0];
        this.invoice_products.byKey(key).then((row) => {
          switch (column) {
            case 'view_order':
              this.view_orderChange();
              break;
            case 'product_name':
              this.product_nameChange(row);
              break;
            case 'unit_code':
              this.unit_codeChange(row);
              break;
            case 'quantity':
            case 'price':
            case 'price_nte':
            case 'vat':
            case 'amount':
            case 'amount_vat':
            case 'amount_after_vat':
            case 'amount_discount':
            case 'discount':
            case 'amount_without_discount':
            case 'other_tax':
            case 'other_charge':
              this._tinhGiaTri();
              break;
            default:
              break;
            // }
          }
        }, (error) => {
          alert(error);
        });
      });
    }, 100);
  }

  onGripKeyDown(e) {
    if (e.event.code === 'Delete') {
      e.event.srcElement.value = null;
    }
  }

  onCellClick(e) {
    if (e.column.dataField === 'school_fee_month') {
      this.selectMonth = false;
      const product = this.select.products.filter(p => p.code === e.row.data.product_code)[0];
      if (product) {
        if (product.form_revenue === sEnum.hinhthucthu.Thang) {
          const initialState = {
            fee_months: this.fee_months,
            month_year: e.row.data.school_fee_month,
            product_code: e.row.data.product_code
          };
          this.bsModalRef = this.modalService.show(SelectMonthComponent, {
            class: 'second child-modal',
            initialState,
            backdrop: 'static',
            keyboard: false,
          });
          let index = 0;
          this.modalService.onHide.subscribe(() => {
            if (!this.selectMonth) {
              index++;
              let tr = index;
              if (index == 1) {
                if (e.row.data.product_code == this.bsModalRef.content.product_code) {
                  if (this.bsModalRef.content.successSaveItem) {
                    e.row.data.school_fee_month = this.bsModalRef.content.successSaveData;
                  }
                  else {
                  }
                  let _lstMonth = [];
                  if (e.row.data.school_fee_month)
                    _lstMonth = e.row.data.school_fee_month.split(';').filter(p => p !== '');
                  if (_lstMonth && _lstMonth.length > 0) {
                    e.row.data.quantity = _lstMonth.length;
                  } else {
                    e.row.data.quantity = null;
                  }
                  if (e.row.data.quantity) {
                    e.row.data.amount = e.row.data.price ? parseFloat(e.row.data.price) * parseFloat(e.row.data.quantity) : 0;
                  } else {
                    e.row.data.amount = e.row.data.price ? parseFloat(e.row.data.price) : 0;
                  }
                  if (this.isHdGTGT) {
                    e.row.data.amount_vat = (e.row.data.vat >= 0) ? this.caculatorAmountVat(e.row.data.amount, e.row.data.vat) : null;
                  } else {
                    e.row.data.amount_vat = null;
                  }
                  e.row.data.amount_after_vat = e.row.data.amount + (e.row.data.amount_vat != null ? e.row.data.amount_vat : 0);
                  this.invoice_products.update(e.row.data.id, e.row.data);
                  this._tinhGiaTri();
                  return true;
                }
              }
            }
          });
        }
      }

    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      e.rowElement.style.whiteSpace = 'pre-wrap';
    }
  }

  onEditorPreparing(e) {
    if (e.dataField === 'product_name' && e.parentType === 'dataRow' && e.editorName !== 'dxSelectBox') {
      e.editorName = 'dxTextArea';
    }
    // if (e.parentType === 'dataRow') {
    //   e.editorOptions.onFocusOut = (args) => {
    //     if (e.dataField === 'unit_code') {
    //       const _obj = this.select.unit_codes.filter(p => p.code === e.row.data.unit_code)[0];
    //       if (_obj) {
    //         e.row.data.unit_name = _obj.name;
    //       } else {
    //         e.row.data.unit_name = '';
    //       }
    //     }
    //     if (e.dataField === 'country_code') {
    //       const _obj = this.select.countrys.filter(p => p.code === e.row.data.country_code)[0];
    //       if (_obj) {
    //         e.row.data.country_name = _obj.name;
    //       } else {
    //         e.row.data.country_name = '';
    //       }
    //     }
    //     this.invoice_products.update(e.row.data.id, e.row.data);
    //   };
    // }
  }

  onEditorPrepared(e) {
    if ((e.dataField === 'amount_without_discount' || e.dataField === 'discount' || e.dataField === 'amount_discount' || e.dataField === 'quantity' || e.dataField === 'price' || e.dataField === 'amount' || e.dataField === 'amount_vat' || e.dataField === 'amount_after_vat') && e.parentType === 'dataRow') {
      e.editorElement.childNodes[1].firstChild.onwheel = (evt) => {
        e.component.cancelEditData();
      };

      e.editorElement.childNodes[1].firstChild.addEventListener('select', function () {
        this.selectionStart = this.selectionEnd;
      }, false);
    }
  }

  onContextMenuPreparing(e) {
    if (e.row.rowType === 'data') {
      e.items = [{
        text: 'Thm hng',
        onItemClick: () => {
          this.addRow();
        }
      },
      {
        text: 'Xa hng',
        onItemClick: () => {
          this.deleteRow(e.row.data);
        }
      }];
    }
  }

  addRow() {
    let len, order;
    if (this.dataSource.items().length >= 1) {
      len = this.dataSource.items().length;
      order = this.dataSource.items()[len - 1].order + 1;
    } else {
      order = 1;
    }
    this.invoice_products.insert(this.frmData.add_invoice_products_data(order, order));
    this.dataSource.reload();
  }

  view_orderChange() {
    if (!this.isTT78) {
      let _index = 0;
      this.dataSource.items().forEach(item => {
        if (item.view_order && VStringService._getValueToString(this._objTemplateSelect.template_type) !== sEnum.templatetype.HoaDonNhaHangKhachSan) {
          item.view_order = ++_index;
        }
        if (VStringService._getValueToString(this._objTemplateSelect.template_type) === sEnum.templatetype.HoaDonNhaHangKhachSan) {
          if (!item.is_money_service) {
            item.view_order = ++_index;
          } else {
            item.view_order = null;
          }
        }
      });
    }
    else {
      let _index = 0;
      this.dataSource.items().forEach(item => {
        if (item.view_order && VStringService._getValueToString(this._oTemplateTT78.template_type) !== sEnum.templatetype.HoaDonNhaHangKhachSan) {
          item.view_order = ++_index;
        }
        if (VStringService._getValueToString(this._oTemplateTT78.template_type) === sEnum.templatetype.HoaDonNhaHangKhachSan) {
          if (!item.is_money_service) {
            item.view_order = ++_index;
          } else {
            item.view_order = null;
          }
        }
      });
    }
  }

  // Thay i ct m hng
  product_nameChange(row) {
    setTimeout(() => {
      this.select.products.some(item => {
        if (item.product_name === row.product_name) {
          row.unit_code = this.checkUnitCode(item.unit_code);
          row.quantity = item.quantity ? parseFloat(item.quantity) : item.quantity;
          row.price = item.price ? parseFloat(item.price) : item.price;
          row.price_nte = item.price_nte ? parseFloat(item.price_nte) : item.price_nte;
          row.amount = item.price ? parseFloat(item.price) : 0;
          row.engine_number = item.engine_number;
          row.school_fee_month = item.school_fee_month;
          row.chassis_number = item.chassis_number;
          row.other_tax = item.other_tax;
          row.product_code = item.product_code;
          row.other_charge = item.other_charge;
          row.semester = item.semester;
          if (this.isHdGTGT) {
            row.vat = item.vat != null ? item.vat.toString() : null;
            row.amount_vat = (row.vat >= 0) ? this.caculatorAmountVat(row.amount, row.vat) : null;
            row.amount_after_vat = row.amount + row.amount_vat;
          } else {
            row.amount_after_vat = row.amount;
          }
          this.invoice_products.update(row.order, row);
          return true;
        }
      });
      this._tinhGiaTri();
    }, 200);

  }

  unit_codeChange(row) {
    let _obj = this.select.unit_codes.filter(p => p.code == row.unit_code)[0];
    if (_obj) {
      row.unit_name = _obj.name;
      this.invoice_products.update(row.id, row);
    } else {
      row.unit_name = '';
      this.invoice_products.update(row.id, row);
    }
  }

  checkUnitCode(unitCode) {
    const items = this.select.unit_codes;
    for (let i = 0; i < items.length; i++) {
      if (items[i].code == unitCode) {
        return unitCode;
      }
    }
    return null;
  }

  _tinhGiaTri() {
    if (!this.isTT78) {
      setTimeout(() => {
        VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, this.select, this._objTemplateSelect, this.configs.system_parameter);
        if (VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBay
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBayHoanTien
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBayThuTien
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBayTongHop
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBayChung
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonVeMayBayHoanVe
          || VStringService._getValueToString(this._objTemplateSelect.template_type) == sEnum.templatetype.HoaDonXangDau) {
          VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._objTemplateSelect.template_type));
        }
      }, 100);
    }
    else {
      setTimeout(() => {
        VChanggeMoneyService._changeMoneyItem(this.dataSource, this.invoice_products, this.frmData, this.formatter, !this.frmData.auto_value, this.frmData.auto_value_price, this.select, this._oTemplateTT78, this.configs.system_parameter);
        if (VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBay
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBayHoanTien
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBayThuTien
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBayTongHop
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBayChung
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonVeMayBayHoanVe
          || VStringService._getValueToString(this._oTemplateTT78.template_type) == sEnum.templatetype.HoaDonXangDau) {
          VChanggeMoneyService._changeMoneyTicket(this.frmData, this.formatter, VStringService._getValueToString(this._oTemplateTT78.template_type));
        }
      }, 100);
    }
  }

  caculatorAmountVat(amount, vat) {
    const temp = amount / 100 * vat;
    return this.roundNumber(temp, 5);
  }

  roundNumber(number, n): number {
    let p = Math.pow(10, n);
    return Math.round(number * p) / p;
  }

  // Bt modal thay i s thp phn
  configDecimal(n) {
    const initialState = {
      column: n,
      formatter: this.formatter,
      showDonGiaNte: this.configs.system_parameter.view_price_nte
    };
    this.bsModalRef = this.modalService.show(ConfigDecimalComponent, {
      class: 'modal-lg', initialState, backdrop: 'static',
      keyboard: false
    });
    this.modalService.onHide.subscribe(() => {
      let data = this.bsModalRef.content.successSaveItem;
      if (data) {
        this.formatter[data.column] = data.character;
        this._tinhGiaTri();
      }
    });
  }

  // Xa hng
  deleteRow(item) {
    // setTimeout(() => {
    const check = confirm('Bn c ng  xa khng?');
    if (check && !this.ttDaXuatHoaDon) {
      setTimeout(() => {
        this.invoice_products.remove(item.id);

        // this.dataSource.store().remove(item.id);
        this.dataSource.reload();
        this._tinhGiaTri();

      }, 100);
    }
    // }, 200);
  }

  ngOnDestroy() {
    this.routeSub.unsubscribe();
  }

  changePhiDV(row) {
    this.view_orderChange();
    if (row.is_money_service) {
      row.commercial_discount = false;
    }
    if (row.is_money_service && VStringService._getValueToString(row.product_name) === '') {
      row.product_name = 'Ph dch v';
      this.invoice_products.update(row.id, row);
    } else if (!row.is_money_service && VStringService._getValueToString(row.product_name) === 'Ph dch v') {
      row.product_name = '';
      this.invoice_products.update(row.id, row);
    }
  }

  cktmChange(row) {
    if (row.commercial_discount) {
      row.is_money_service = false;
    }
  }
}
