import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { HandleInvoiceService } from '@modules/handle-invoice/shared';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AuthService, Library, VPreviewInvService, SocketService, sEnum } from '@core/index';
import { VatService } from '@modules/vat/shared';
import { TranslateService } from '@ngx-translate/core';
import { ViewAttachFileComponent } from '@shared/modules/vat-shared/components/view-attach-file/view-attach-file.component';
import { HanldeReportService } from '@modules/handle-report/hanlde-report.service';
import { Helper } from '@core/helper/Helper';
import { finalize, map } from 'rxjs/operators';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { SendInvoicesComponent } from '@modules/handle-invoice/components/send-invoices/send-invoices.component';
import { HandleInvoiceStudentService } from '@modules/handle-invoice-student/shared/handle-invoice-student.service';
import { SendDataForTaxComponent } from '@shared/index';
import { TemplateService } from '@modules/template/shared';
import { ReportTaxInvoiceErrorComponent } from '@modules/handle-invoice/components/report-tax-invoice-error/report-tax-invoice-error.component'
import { NgxSpinnerService } from 'ngx-spinner';
import { Subscription } from 'rxjs/Subscription';
import { DxDataGridComponent } from 'devextreme-angular';
import { RemoveComponent } from '../remove/remove.component';
import { ViewInvoiceService } from '@shared/service/view-invoice.service';
import { InvoiceService } from '@shared/service/invoice.service';
@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  pageSize: any;
  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalRef: BsModalRef;
  paramSearch: any;
  _translate: any;
  clickCurent: any = undefined;
  idRow: any = undefined;
  checkSubmit: any;
  private fileName: string;
  arrayFileContent: any;
  formFileContent: FormGroup;
  configs: any = {};
  isSearchStudent = true;
  currentUser: any;
  isDaDKTT78: boolean = false;
  infoDangKy: any;
  isExistFormRelease: boolean = false;
  bsModalSignRef: BsModalRef;
  ioSignature: Subscription;
  dataInfor: any;
  pageSelect: any;
  isDisableAdjust = true;
  isDisableReplace = true;
  objStatusBtn = {};
  constructor(
    private authService: AuthService,
    private handleInvoiceService: HandleInvoiceService,
    private vatService: VatService,
    private modalService: BsModalService,
    private router: Router,
    public translate: TranslateService,
    private handleReportService: HanldeReportService,
    private handleStudentService: HandleInvoiceStudentService,
    private fb: FormBuilder,
    private templateService: TemplateService,
    private spinner: NgxSpinnerService,
    private socketService: SocketService,
    private vPreviewInvService: ViewInvoiceService,
    private invoiceService: InvoiceService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.formFileContent = this.fb.group(this.handleReportService.generatedFormControlAll);
    this.configs = this.authService.getConfigs();
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.currentUser = this.authService.getCurrentUser();
    if (this.currentUser.is_convert_TT78) {
      this.infoDangKy = this.authService.getInfoRegister();
      if (this.infoDangKy && this.infoDangKy.id) {
        if (this.infoDangKy.status == "CHAP_NHAN") {
          this.isDaDKTT78 = true;
          let infoFormRelease = this.authService.getInfoFormRelease();
          if (infoFormRelease && infoFormRelease.invoice_releases && infoFormRelease.invoice_releases.length > 0) {
            let invoicesReleases = infoFormRelease.invoice_releases.filter(p => (parseInt(p.amount) - parseInt(p.used_amount)) > 0);
            if (invoicesReleases && invoicesReleases.length > 0)
              this.isExistFormRelease = true;
          }
        }
      }
    }
    /* Hin nt iu chnh/thay th */
    if (this.configs.system_parameter.incurred_adjustment_invoice || this.configs.system_parameter.process_not_in_software) {
      this.isDisableAdjust = false;
      if (this.configs.system_parameter.process_not_in_software)
        this.isDisableReplace = false;
    }
  }

  // HS-SV
  loadList(params?) {
    this.dataSource = {};
    this.dataSource.store = this.handleStudentService.getAllStudent(params);
  }

  filterReport(data, _condition) {
    if (data && data.length > 0) {
      return data.filter(p => p.is_paper_report === _condition && p.status === 'HOAT_DONG');
    } else {
      return [];
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.invoice_number) {
            if (e.data.send_department_status == "DA_GUI_CQT")
              e.rowElement.style.color = '#008B8B';
            else if (e.data.send_department_status == "DA_CAP")
              e.rowElement.style.color = 'black';
            else if (e.data.send_department_status == "DA_TIEP_NHAN")
              e.rowElement.style.color = 'black';
            else
              e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        let eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }

  trangthaihoadon(data) {
    switch (data.status) {
      case 'DA_XUAT':
        break;
      case 'GHI_TAM':
        if (data.invoice_number) {
          return this._translate.GRID.INVOICE.signed_error;
        } else {
          return this._translate.CONTROLL.BUTTON.save_temporary;
        }
      case 'GIU_SO':
        return this._translate.GRID.INVOICE.wait_for_sign;
      default:
        break;
    }
  }

  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }

  checkDisableButton() {
    if (this.selectedItems.length) {
      if (this.selectedItems[0].status == 'DA_XUAT') {
        return true;
      }
      return false;
    }
    return false;
  }

  myFunction(e) {
    if (this.idRow && document.getElementById(this.idRow)) {
      document.getElementById(this.idRow).classList.remove('show');
    }
    if (this.clickCurent !== undefined && this.clickCurent !== e && document.getElementById(this.clickCurent)) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    document.getElementById(e).classList.toggle('show');
    this.clickCurent = e;
  }

  myFunction1(e) {
    if (this.clickCurent && document.getElementById(this.clickCurent)) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.idRow !== '' && this.idRow !== e && document.getElementById(this.idRow)) {
      document.getElementById(this.idRow).classList.remove('show');
    }
    document.getElementById(e).classList.toggle('show');
    this.idRow = e;
  }

  // Chn i tng trong mn hnh danh sch
  selectionChanged(data: any) {
    this.hidenDropdownList();
    this.selectedItems = data.selectedRowsData;
    if (this.selectedRows.length == 1 || this.configs.system_parameter.incurred_adjustment_invoice || this.configs.system_parameter.process_not_in_software) {
      if (this.selectedRows.length == 1 || this.configs.system_parameter.process_not_in_software) {
        this.isDisableAdjust = false;
        this.isDisableReplace = false;
      }
      else {
        this.isDisableAdjust = false;
        this.isDisableReplace = true;
      }
    }
    else {
      this.isDisableAdjust = true;
      this.isDisableReplace = true;
    }
  }

  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }

  routerCancelInvoice() {
    if (this.selectedItems && this.selectedItems.length > 0) {
      let lstGhiTam = this.selectedItems.filter(p => p.status == 'GHI_TAM');
      if (lstGhiTam && lstGhiTam.length == this.selectedItems.length) {
        const ids = this.selectedRows.join(',');
        const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.handleInvoiceService.delete(ids).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                this.loadList(this.paramSearch);
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      }
      else {
        let lstInvoice = this.selectedItems.filter(p => p.status == 'DA_XUAT' && p.adjustment_type == '1');
        if (lstInvoice && lstInvoice.length > 0) {
          if (lstInvoice.length == 1) {
            const row = lstInvoice[0];
            this.setUrlNavigate();
            this.router.navigate(['/system/handle-report/', row.id, 'HUY']);
          }
          else {
            let _lstCancel = lstInvoice.filter(p => p.adjustment_type == '7' || p.adjustment_type == '2');
            if (_lstCancel && _lstCancel.length > 0) {
              Library.notify(this._translate.NOTIFY.choose_cancel_invoice, 'error');
            }
            else
              this.delete(lstInvoice);
          }

        }
        else
          Library.notify('Bn khng th hy ha n ny', 'error');
      }
    }
  }

  dieuChinh() {
    if (this.isExistFormRelease) {
      Library.notify("Vui lng hy di ha n theo TT32 cha s dng trc khi lp ha n theo TT78", "error", 5000);
      return;
    }
    if (this.selectedItems && this.selectedItems.length == 0) {
      if (this.configs.system_parameter.process_not_in_software) {
        this.router.navigate(['/system/handle-invoice/adjust-without-software']);
      }
      else if (this.configs.system_parameter.incurred_adjustment_invoice)
        this.router.navigate(['/system/handle-invoice/multiple-adjust']);
    }
    else {
      const row = this.selectedItems[0];
      if (row.status === 'DA_XUAT' && row.adjustment_type !== '7' && row.adjustment_type !== '2') {
        const template_code = row.template_code.split('/');
        switch (template_code[0]) {
          case '01GTKT0':
          case '02GTTT0':
          case '1':
          case '2':
            this.setUrlNavigate();
            this.router.navigate(['/system/handle-report/', row.id, 'DIEU_CHINH']);
            break;
          // case '02GTTT0':
          //   this.router.navigate(['/system/handle-report/', row.id, 'DIEU_CHINH']);
          //   break;
          default:
            break;
        }
      } else {
        Library.notify('Bn khng th iu chnh ha n ny', 'error');
      }
    }
  }

  thayThe() {
    if (this.isExistFormRelease) {
      Library.notify("Vui lng hy di ha n theo TT32 cha s dng trc khi lp ha n theo TT78", "error", 5000);
      return;
    }
    if (this.selectedItems && this.selectedItems.length == 0 && this.configs.system_parameter.process_not_in_software) {
      this.router.navigate(['/system/handle-invoice/replace-without-software']);
    }
    else {
      const row = this.selectedItems[0];
      if (row.status === 'DA_XUAT' && row.adjustment_type !== '4') {
        this.router.navigate([`/system/handle-invoice/replace/${row.id}`]);
        const template_code = row.template_code.split('/');
        switch (template_code[0]) {
          case '01GTKT0':
          case '02GTTT0':
          case '2':
          case '1':
            this.setUrlNavigate();
            this.router.navigate(['/system/handle-report/', row.id, 'THAY_THE']);
            break;
          // case '02GTTT0':
          //   this.router.navigate([`/system/handle-invoice/sale-replace/${row.id}`]);
          //   break;
          default:
            break;
        }
      } else {
        Library.notify('Bn khng th thay th ha n ny', 'error');
      }
    }
  }

  editNotSelect(row) {
    if (this.isExistFormRelease) {
      Library.notify("Vui lng hy di ha n theo TT32 cha s dng trc khi lp ha n theo TT78", "error", 5000);
      return;
    }
    this.hidenDropdownList();
    if (row.status === 'DA_XUAT') {
      Library.notify('Bn khng th sa ha n  xut', 'error');
    } else {
      if (row.adjustment_type === '5') {
        if (row.is_adjustment_invoices || row.is_outside_process) {
          if (row.is_adjustment_invoices)
            this.router.navigate([`/system/handle-invoice/multiple-adjust/${row.id}`]);
          else
            this.router.navigate([`/system/handle-invoice/adjust-without-software/${row.id}`]);
        }
        else {
          switch (row.invoice_type) {
            case '01GTKT':
              if (row.batch_id)
                this.router.navigate([`/system/handle-invoice-student/edit-adjust-vat/${row.id}`]);
              else
                this.router.navigate([`/system/handle-invoice/edit-adjust/${row.id}`]);
              break;
            case '02GTTT':
              if (row.batch_id)
                this.router.navigate([`/system/handle-invoice-student/edit-adjust-sale/${row.id}`]);
              else
                this.router.navigate([`/system/handle-invoice/edit-sale-adjust/${row.id}`]);
              break;
            default:
              break;
          }
        }
      } else if (row.adjustment_type === '3') {
        if (row.is_outside_process) {
          this.router.navigate([`/system/handle-invoice/replace-without-software/${row.id}`]);
        }
        else {
          switch (row.invoice_type) {
            case '01GTKT':
              if (row.batch_id)
                this.router.navigate([`/system/handle-invoice-student/edit-replace-vat/${row.id}`]);
              else
                this.router.navigate([`/system/handle-invoice/edit-replace/${row.id}`]);
              break;
            case '02GTTT':
              if (row.batch_id)
                this.router.navigate([`/system/handle-invoice-student/edit-replace-sale/${row.id}`]);
              else
                this.router.navigate([`/system/handle-invoice/edit-sale-replace/${row.id}`]);
              break;
            default:
              break;
          }
        }
      }
    }
  }

  viewNotSelect(row) {
    this.hidenDropdownList();
    if (row.status == 'DA_XUAT') {
      let ids = [];
      ids.push(row.id);
      if (this.configs.system_parameter.view_multi_invoice) {
        let data = this.vatService.taodulieuXemMau(ids);
        data['configs'] = sEnum.cauhinhxemmau.hoadon;
        this.vPreviewInvService.xemMau(data);
      }
      else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.xem_hoa_don
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    } else {
      Library.notify('Ha n b k li, bn vui lng k li ha n', 'error');
    }
  }

  view(data) {
    if (data) {
      this.dataInfor = data;
      const ids = [];
      ids.push(data.id);
      if (this.configs.system_parameter.view_multi_invoice) {
        let data = this.vatService.taodulieuXemMauHS(ids);
        data['configs'] = sEnum.cauhinhxemmau.hoadon;
        this.vPreviewInvService.xemMau(data);
      }
      else {
        const initialState = {
          items: ids,
          isXuatHD: false,
          hinhthucxuat: '',
          typeAction: sEnum.typeCode.xem_hoa_don
        };
        this.vPreviewInvService.xemMauPhanTrang(initialState);
      }
    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  viewDetailInvoice(row) {
    this.router.navigate([`/system/vat-invoice/${row.id}`]);
  }

  viewReport(idInv, data) {
    const url = window.location.pathname;
    let type;
    if (data.type === 'HUY') {
      type = 'THAY_THE';
    } else {
      type = data.type;
    }
    if (sessionStorage.hasOwnProperty('urlCreate')) {
      if (sessionStorage.getItem('urlCreate') !== null) {
        sessionStorage.setItem('urlCreate', url);
      } else {
        sessionStorage.setItem('urlCreate', url);
      }
    } else {
      sessionStorage.setItem('urlCreate', url);
    }
    this.handleReportService.createReport(idInv, type);
  }

  sendInvoice(id) {
    Library.showloading();
    let sub = this.vatService.showToSendInvoice(id).subscribe(rs => {
      if (rs.status == 'success') {
        const initialState = {
          invoice: rs.data.invoice,
        };
        this.bsModalRef = this.modalService.show(SendInvoicesComponent, {
          class: 'modal-lg full-modal', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      } else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  delete(lstInvoice) {
    this.hidenDropdownList();
    let ids = [];
    lstInvoice = lstInvoice.sort((invA, invB) => {
      return <any>new Date(invB.invoice_date) - <any>new Date(invA.invoice_date) || invB.invoice_number.localeCompare(invA.invoice_number);
    });

    ids = lstInvoice.map(e => e.id);
    Library.showloading();
    let isView = false;
    const sub = this.handleInvoiceService.showHDXoaBo(ids, isView).subscribe(rs => {
      // let countReport = 0;
      // if (rs[1].data.invoice_reports)
      //   countReport = rs[1].data.invoice_reports.length;
      let lstInvoice = rs[0].data.invoices.filter(p => p.adjustment_type == '7');
      if (lstInvoice && lstInvoice.length > 0)
        isView = true;
      let lstInv = [];
      ids.forEach(id => {
        let inv = rs[0].data.invoices.filter(p => p.id == id)[0];
        lstInv.push(inv);
      });
      const initialState = {
        isView: isView,
        data: lstInv,
        configs: this.configs,
        infoDangKy: this.infoDangKy
        //  countReport: countReport,
      };
      this.bsModalRef = this.modalService.show(RemoveComponent, {
        class: 'modal-lg full-width', initialState, backdrop: 'static',
        keyboard: false
      });
      this.modalService.onHide.subscribe(() => {
        if (this.bsModalRef.content.successSaveItem) {
          this.loadList(this.paramSearch);
        }
      });
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }

  viewAttachFile(data) {
    this.hidenDropdownList();
    if (data) {
      Library.showloading();
      this.vatService.showAttachFile(data.id).subscribe((rs: any) => {
        if (rs.status == 'success') {
          if (rs.data && rs.data.invoice && rs.data.invoice.invoice_files.length > 0) {
            let initialState = {
              fileData: rs.data.invoice.invoice_files,
              view_attach_file: true,
            };
            this.bsModalRef = this.modalService.show(ViewAttachFileComponent, {
              backdrop: 'static',
              keyboard: false,
              class: 'modal-lg',
              initialState
            });

          } else {
            Library.notify(this._translate.NOTIFY.not_file_attach, 'error');
          }
        } else {
          Library.notify(this._translate.NOTIFY.an_error_occurred, 'error');
        }

      }, err => {
      }, () => {
        Library.hideloading();
      });
    }
  }

  inChuyenDoi(data) {
    if (this.selectedItems.length || data) {
      let _lst = this.selectedItems.filter(p => p.status == 'DA_XUAT');
      if (_lst.length > 0 || data) {
        let ids = [];
        if (data) {
          if (data.status == 'DA_XUAT') {
            ids.push(data.id);
          } else {
            Library.notify(this._translate.NOTIFY.not_print_convert, 'error');
            return;
          }
        } else {
          ids = _lst.map(e => e.id);
        }
        if (this.configs.system_parameter.view_multi_invoice) {
          const data = this.vatService.taodulieuInchuyendoiHS(ids);
          data['configs'] = sEnum.cauhinhxemmau.hoadon;
          this.vPreviewInvService.xemMau(data);
        }
        else {
          const initialState = {
            items: ids,
            isXuatHD: false,
            hinhthucxuat: '',
            typeAction: sEnum.typeCode.in_chuyen_doi_hs
          };
          this.vPreviewInvService.xemMauPhanTrang(initialState);
        }
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }

    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  customizeText = (url) => {
    this.invoiceService.handleTotalGrid(url.value, this.objStatusBtn, this.dataGrid);
    return '';
  }

  downloadFile(data, isBBGiay) {
    this.hidenDropdownList();
    Library.showloading();
    this.handleReportService.getAllFileContentOfReport(data.id, isBBGiay)
      .pipe(
        map(value => {
          if (value.status === 'success') {
            return value.data.invoice_reports;
          }
        }),
        finalize(() => Library.hideloading())
      )
      .subscribe(
        value => {
          this.arrayFileContent = [];
          for (let i = 0; i < value.length; i++) {
            if (value[i].file_content !== null) {
              if (value[i].type === 'DIEU_CHINH') {
                this.arrayFileContent.push({ id: value[i].id, file_content: value[i].file_content, file_name: value[i].file_name });
              } else {
                this.arrayFileContent.push({ id: value[i].id, file_content: value[i].file_content, file_name: value[i].file_name });
              }
            }
          }

          const initialState = {
            fileData: this.arrayFileContent,
            view_attach_file: true,
            view_paper_report: (isBBGiay == '1' ? true : false),
            _objInv: data
          };
          this.bsModalRef = this.modalService.show(ViewAttachFileComponent, {
            backdrop: 'static',
            keyboard: false,
            class: 'modal-lg',
            initialState
          });

        },
        error => {
          Library.notify(error, 'error');
        }
      );
  }

  dieuChinhBtn(data) {
    const row = data;
    if (row.status === 'DA_XUAT' && row.adjustment_type !== '7' && row.adjustment_type !== '2') {
      const template_code = row.template_code.split('/');
      switch (template_code[0]) {
        case '01GTKT0':
        case '1':
          this.router.navigate(['/system/handle-report/', row.id, 'DIEU_CHINH']);
          break;
        case '02GTTT0':
        case '2':
          this.router.navigate([`/system/handle-invoice/sale-adjust/${row.id}`]);
          break;
        default:
          break;
      }
    } else {
      Library.notify('Bn khng th iu chnh ha n ny', 'error');
    }
  }

  thayTheBtn(data) {
    const row = data;
    if (row.status === 'DA_XUAT' && row.adjustment_type !== 7 && row.adjustment_type !== 2 && row.adjustment_type !== 4 && row.adjustment_type !== 5) {
      this.router.navigate([`/system/handle-invoice/replace/${row.id}`]);
      const template_code = row.template_code.split('/');
      switch (template_code[0]) {
        case '01GTKT0':
        case '1':
          this.router.navigate(['/system/handle-report/', row.id, 'THAY_THE']);
          break;
        case '02GTTT0':
        case '2':
          this.router.navigate([`/system/handle-invoice/sale-replace/${row.id}`]);
          break;
        default:
          break;
      }
    } else {
      Library.notify('Bn khng th thay th ha n ny', 'error');
    }
  }

  cancelReport(data) {
    const row = data;
    if (row.status === 'DA_XUAT' && row.adjustment_type !== '7') {
      this.router.navigate(['/system/handle-report/', row.id, 'HUY']);
    } else {
      Library.notify('Ha n  b hy', 'error');
    }
  }

  dinhKemFile(evt) {
    evt.srcElement.firstChild.click();
    // document.getElementById("file-dinh-kem").click();
    // console.log(e)

  }

  onDinhKemFile(e) {
    const rowIndex = e.target.dataset.rowindex;
    const fileAttachData: Array<any> = [];
    const fileReader = new FileReader();
    const file = e.target.files[0];
    if (file.size <= 2000000) {
      const pattFile = /(\.xlsx|\.docx|\.doc|\.pdf|\.xls|\.xml)$/i;
      if (pattFile.test(file.name)) {
        fileReader.readAsDataURL(file);
        fileReader.onload = (e) => {
          const base64result = fileReader.result.toString().split(',')[1];
          fileAttachData.unshift({
            type: 'DK',
            file_name: file.name,
            file_content: base64result,
          });
          const data = {
            invoice_report: {
              invoice_id: rowIndex,
              type: 'HUY',
              status: 'HOAT_DONG',
              signature_status: 'NGUOI_BAN_KY',
              file_content: base64result,
              is_paper_report: '1',
              file_name: file.name
            }
          };
          this.handleReportService.apiCreateReport(data).pipe(finalize(() => Library.hideloading()))
            .subscribe(value => {
              if (value.status === 'success') {
                this.loadList(this.paramSearch);
                Library.notify(this._translate.NOTIFY.attach_paper_report, 'success');
              } else {
                Library.notify(value.message, 'error');
              }
            },
              error => {
                Library.notify(error, 'error');
              }
            );
        };
      } else {
        Library.notify('File bn chn khng ng nh dng xls, xlsx, doc, docx, pdf, xml', 'error');
      }
    } else {
      Library.notify('Yu cu file nh hn 2MB', 'error');
    }
  }

  downloadFileDocx(data, type) {
    const dataSeller = JSON.parse(window.localStorage.getItem('currentUser'));

    const a = {
      invoice_report: {
        invoice_id: data.id,
        file_extension: 'docx',
        doc_relate_date: moment(new Date()).format('YYYY-MM-DD'),
        type: type,
        json_data: {
          seller_name: dataSeller.account.name,
          seller_address: dataSeller.account.business_address,
          seller_tax_code: dataSeller.account.tax_code,
          seller_mobile: dataSeller.account.contact_mobile,
          seller_surrogate_name: dataSeller.account.surrogate_name,
          seller_surrogate_position: dataSeller.account.surrogate_position,
          buyer_name: data.hasOwnProperty('customer_name') && data.customer_name !== null ? data.customer_name : data.buyer_name,
          buyer_address: data.hasOwnProperty('buyer_address') ? data.buyer_address : null,
          buyer_tax_code: data.hasOwnProperty('buyer_tax_code') ? data.buyer_tax_code : null,
          buyer_mobile: data.hasOwnProperty('buyer_mobile') ? data.buyer_mobile : null,
          buyer_surrogate_name: data.hasOwnProperty('buyer_surrogate_name') ? data.buyer_surrogate_name : null,
          buyer_surrogate_position: data.hasOwnProperty('buyer_surrogate_position') ? data.buyer_surrogate_position : null,
          template_code: data.hasOwnProperty('template_code') ? data.template_code : null,
          invoice_series: data.hasOwnProperty('invoice_series') ? data.invoice_series : null,
          invoice_date: data.hasOwnProperty('invoice_date') ? data.invoice_date : null,
          invoice_number: data.hasOwnProperty('invoice_number') ? data.invoice_number : null,
        },
        is_paper_report: 1
      }
    };
    Library.showloading();
    this.handleReportService.apiGetFileContent(a).subscribe(
      value => {
        if (value.status === 'success') {
          Helper.dowloadFile(value.data.invoice.file_name, 'docx', value.data.invoice.file_content);
        } else {
          Library.notify(value.message, 'error');
        }
      }, error => {
        Library.notify(error.message, 'error');

      }, () => {
        Library.hideloading();
      }
    );
  }

  hidenDropdownList() {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.idRow) {
      document.getElementById(this.idRow).classList.remove('show');
    }
  }
  LydoKTiepNhan(data) {
    let lstErr = JSON.parse(data.error_content);
    const initialState = {
      lstErr: lstErr,
      notifyType: "THONG_BAO_LOI_CQT"
    };
    this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
      backdrop: 'static',
      keyboard: true,
      class: 'modal-lg modal-margin-width',
      initialState
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
  }
  BaoCaoCQT() {
    this.hidenDropdownList();
    Library.showloading();
    let sub = this.templateService.getAll1().subscribe(rs => {
      if (rs.status == 'success') {
        let listTemplate = rs.data.templates;
        let lstInvoice = this.selectedItems;
        lstInvoice = lstInvoice.sort((invA, invB) => {
          return <any>new Date(invB.invoice_date) - <any>new Date(invA.invoice_date) || invB.invoice_number.localeCompare(invA.invoice_number);
        });
        const initialState = {
          data: lstInvoice,
          configs: this.configs,
          listTemplate: listTemplate,
          infoDangKy: this.infoDangKy
        };
        this.bsModalRef = this.modalService.show(ReportTaxInvoiceErrorComponent, {
          class: 'modal-lg full-width', initialState, backdrop: 'static',
          keyboard: false
        });
        this.modalService.onHide.subscribe(() => {
          if (this.bsModalRef.content.successSaveItem) {
            this.loadList(this.paramSearch);
          }
        });
      }
      else {
        Library.notify(rs.message, 'error');
      }
    }, err => {
    }, () => {
      Library.hideloading();
      sub.unsubscribe();
    });
  }
  ngOnDestroy() {
  }
  onContentReady(e) {
    if (e) {
      const dataSource = this.dataGrid.instance.getDataSource();
      this.pageSize = dataSource.items().length
    }
  }
  ThongDiepGuiNhan(data) {
    Library.showloading();
    if (data.invoice_wrongs && data.invoice_wrongs.length > 0) {
      let lstErr = data.invoice_wrongs;
      const initialState = {
        lstErr: lstErr,
        notifyType: "THONG_DIEP_TRUYEN_NHAN_MTT",
      };
      this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
        backdrop: 'static',
        keyboard: true,
        class: 'modal-lg truyen-nhan',
        initialState
      });
      const subModal = this.modalService.onHide.subscribe(() => {
        subModal.unsubscribe();
      });
      Library.hideloading();
    }
    else {
      let message = {
        message_code: data.message_wrong_code
      }
      let sub = this.handleInvoiceService.getInforSendRecieve(message).subscribe(
        (rs: any) => {
          if (rs.status == "success") {
            let lstErr = rs.data;
            const initialState = {
              lstErr: lstErr,
              notifyType: "THONG_DIEP_TRUYEN_NHAN",
              send_wrong_status: data.send_wrong_status,
              noi_dung_loi: data.error_content
            };
            this.bsModalSignRef = this.modalService.show(SendDataForTaxComponent, {
              backdrop: 'static',
              keyboard: true,
              class: 'modal-lg modal-margin-width truyen-nhan',
              initialState
            });
            const subModal = this.modalService.onHide.subscribe(() => {
              subModal.unsubscribe();
            });
          }
          else {
            Library.notify(rs.message, "error");
          }
        },
        err => {

        },
        () => {
          Library.hideloading();
          sub.unsubscribe();
        }
      );
    }
  }
  deleteItem(data) {
    let ids = data.id;
    const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.handleInvoiceService.delete(ids).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.delete_success, 'success');
            this.loadList(this.paramSearch);
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }
  getStatusSendTax(data) {
    let status = "";
    if (data.invoice_wrongs && data.invoice_wrongs.length > 0) {
      switch (data.invoice_wrongs[0].status) {
        case "DA_GUI":
        case "DA_GUI_CQT":
          status = " gi TB";
          break;
        case "KHONG_TIEP_NHAN":
          status = "CQT khng tip nhn";
          break;
        case "TIEP_NHAN":
          status = "CQT tip nhn";
          break;
        case "CHAP_NHAN":
          status = "CQT chp nhn";
          break;
        case "KHONG_CHAP_NHAN":
          status = "CQT khng chp nhn";
          break;
      }
    }
    else {
      switch (data.send_wrong_status) {
        case "DA_GUI":
        case "DA_GUI_CQT":
          status = " gi TB";
          break;
        case "KHONG_TIEP_NHAN":
          status = "CQT khng tip nhn";
          break;
        case "TIEP_NHAN":
          status = "CQT tip nhn";
          break;
        case "CHAP_NHAN":
          status = "CQT chp nhn";
          break;
        case "KHONG_CHAP_NHAN":
          status = "CQT khng chp nhn";
          break;
      }
    }
    return status;
  }

}
