import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { Library } from '@core/library';
import { map, take } from 'rxjs/operators';
import { HandleInvoiceStudentService } from '@modules/handle-invoice-student/shared/handle-invoice-student.service';
import { Helper } from '@core/helper/Helper';
import { AuthService } from '@core/index';

@Injectable({
  providedIn: 'root'
})
export class AdjustInvoiceStudentResolverService implements Resolve<any[]> {

  constructor(
    private router: Router,
    private handleInvoiceService: HandleInvoiceStudentService,
    private authService: AuthService,
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> | Promise<any[]> | any[] {
    Library.showloading();
    let check = false;
    const origin_id = route.paramMap.get('originId');
    const invoiceId = route.paramMap.get('invoiceId');
    const loaiHoaDon = route.data.type;
    let isTT78 = false;
    let isViewTT32 = false;
    if (route.queryParams['status'] === 'viewInvTT32') {
      isViewTT32 = true;
    }
    let infoDangKy = this.authService.getInfoRegister();
    if (infoDangKy && infoDangKy.id) {
      if (infoDangKy.status == "CHAP_NHAN")
        isTT78 = true;
    }
    if (isTT78 == true && isViewTT32)
      isTT78 = false;
    if (invoiceId) {
      check = true;
    }
    if (isTT78) {
      return this.handleInvoiceService.showInvoiceAdjustResolverTT78(origin_id || invoiceId, check, loaiHoaDon)
        .pipe(
          take(1),
          map(rs => {
            Library.hideloading();
            if (rs[0].status === 'success' && rs[1].status === 'success' && rs[2].status === 'success' && rs[3].status === 'success') {
              let templates = Helper.convertTemplatesToInvoiceResolver(rs[3]);
              return [
                templates,
                rs[0].data,
                '',
                rs[1].data.invoice,
                rs[2].data.invoice_reports
              ];
            } else { // error
              if (loaiHoaDon === '01GTKT') {
                this.router.navigate(['/system/vat-invoice']);
              } else {
                this.router.navigate(['/system/sale-invoice']);
              }

              return null;
            }
          })
        );
    }
    else {
      return this.handleInvoiceService.showInvoiceAdjustResolver(origin_id || invoiceId, check, loaiHoaDon)
        .pipe(
          take(1),
          map(rs => {
            Library.hideloading();
            if (rs[0].status === 'success' && rs[1].status === 'success' && rs[2].status === 'success' && rs[3].status === 'success') {
              const invoice_releases = Helper.convertDataToInvoiceResolver(rs[0]);
              return [
                invoice_releases,
                rs[1].data,
                '',
                rs[2].data.invoice,
                rs[3].data.invoice_reports,
              ];
            } else { // error
              if (loaiHoaDon === '01GTKT') {
                this.router.navigate(['/system/vat-invoice']);
              } else {
                this.router.navigate(['/system/sale-invoice']);
              }

              return null;
            }
          })
        );
    }

  }
}
