  // Cc hm chc nng chnh
  getDataPreview() {
    let formatter: any = {};
    formatter = this.configs.system_parameter.decpoint_is_dot ? { decimal: '.', thousands: ',' } : { decimal: ',', thousands: '.' };

    const data = JSON.parse(JSON.stringify(this.frmData));
    const decimal_quatity = VChanggeMoneyService.getFormatterToNumber(this.formatter.so_luong);
    const decimal_price = VChanggeMoneyService.getFormatterToNumber(this.formatter.don_gia);
    const decimal_amount = VChanggeMoneyService.getFormatterToNumber(this.formatter.thanh_tien);
    const decimal_amount_vat = VChanggeMoneyService.getFormatterToNumber(this.formatter.tien_thue);
    const decimal_amount_after_vat = VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien);
    // let decimal_amount =  VChanggeMoneyService.getFormatterToNumber(this.formatter.thanh_tien);
    let total_amount_product_with_vatx = null, total_amount_product_with_vat0 = null, total_amount_product_with_vat5 = null,
      total_amount_product_with_vat10 = null, amount_vat0 = null;


    data.invoice_products.forEach(item => {
      if (item.vat === -1) {
        total_amount_product_with_vatx += item.amount_after_vat;
      }
      if (item.vat === 0) {
        total_amount_product_with_vat0 += item.amount_after_vat;
      }
      if (item.vat === 0) {
        amount_vat0 += item.amount_vat;
      }
      if (item.vat === 5) {
        total_amount_product_with_vat5 += item.amount_after_vat;
      }
      if (item.vat === 10) {
        total_amount_product_with_vat10 += item.amount_after_vat;
      }
      item['unit_name'] = '';
      this.select.unit_codes.forEach(unit => {
        item.discount = '';
        item.total_discount = '';
        if (unit.code === item.unit_code) {
          item['unit_name'] = unit.name;
        }
      });
      item.amount = Helper.formatMoney(item.amount, decimal_amount, formatter);
      item.price = Helper.formatMoney(item.price, decimal_price, formatter);
      item.quantity = Helper.formatMoney(item.quantity, decimal_quatity, formatter);
      item.amount_vat = Helper.formatMoney(item.amount_vat, 0, formatter);
      item.amount_after_vat = Helper.formatMoney(item.amount_after_vat, decimal_amount_after_vat, formatter);
    });

    data.total_amount_product_with_vatx = Helper.formatMoney(total_amount_product_with_vatx, decimal_amount_after_vat, formatter);
    data.total_amount_product_with_vat0 = Helper.formatMoney(total_amount_product_with_vat0, decimal_amount_after_vat, formatter);
    data.total_amount_product_with_vat5 = Helper.formatMoney(total_amount_product_with_vat5, decimal_amount_after_vat, formatter);
    data.total_amount_product_with_vat10 = Helper.formatMoney(total_amount_product_with_vat10, decimal_amount_after_vat, formatter);
    data.amount_vat0 = amount_vat0;

    data.total_amount_product_vatx = Helper.formatMoney(data.total_amount_product_vatx, decimal_amount, formatter);
    data.total_amount_product_vat0 = Helper.formatMoney(data.total_amount_product_vat0, decimal_amount, formatter);
    data.total_amount_product_vat5 = Helper.formatMoney(data.total_amount_product_vat5, decimal_amount, formatter);
    data.total_amount_product_vat10 = Helper.formatMoney(data.total_amount_product_vat10, decimal_amount, formatter);
    data.amount_vat5 = Helper.formatMoney(data.amount_vat5, decimal_amount_vat, formatter);
    data.amount_vat10 = Helper.formatMoney(data.amount_vat10, decimal_amount_vat, formatter);
    data.amount = Helper.formatMoney(data.amount, decimal_amount, formatter);
    data.total_amount_vat = Helper.formatMoney(data.total_amount_vat, decimal_amount_vat, formatter);
    data.total_payment = Helper.formatMoney(data.total_payment, decimal_amount_after_vat, formatter);

    return data;
  }

