import {RouterModule, Routes} from '@angular/router';
import {NgModule} from '@angular/core';
import {VatDeactivateGuard} from '@modules/vat/deactivate/vat-deactivate.guard';
import {ReplaceInvoiceResolver} from '@modules/handle-invoice/shared';
import {IndexComponent} from '@modules/handle-invoice-student/components/index/index.component';
import {AdjustInvoiceComponent} from '@modules/handle-invoice-student/components/adjust/adjust-invoice/adjust-invoice.component';
import {ReplaceInvoiceComponent} from '@modules/handle-invoice-student/components/replace/replace-invoice/replace-invoice.component';
import {AdjustInvoiceStudentResolverService} from '@modules/handle-invoice-student/shared/adjust-invoice-student-resolver.service';
import {ReplaceInvoiceStudentResolver} from '@modules/handle-invoice-student/shared/replace-invoice-student-resolver.service';

const routes: Routes = [
  {path: '', component: IndexComponent},
  {
    path: 'adjust-vat/:originId',
    component: AdjustInvoiceComponent,
    resolve: {init: AdjustInvoiceStudentResolverService},
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '01GTKT',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'adjust-sale/:originId',
    component: AdjustInvoiceComponent,
    resolve: {init: AdjustInvoiceStudentResolverService},
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '02GTTT',
      brec: 'create_invoice_adjustment',
      status: 'dieuchinhmoi'
    }
  },
  {
    path: 'replace-vat/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ReplaceInvoiceStudentResolver},
    data: {
      type: '01GTKT',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'replace-sale/:originId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ReplaceInvoiceStudentResolver},
    data: {
      type: '02GTTT',
      brec: 'create_invoice_instead',
      status: 'thaythemoi'
    }
  },
  {
    path: 'edit-adjust-vat/:invoiceId',
    component: AdjustInvoiceComponent,
    resolve: {init: AdjustInvoiceStudentResolverService},
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '01GTKT',
      brec: 'create_invoice_adjustment',
    }
  },
  {
    path: 'edit-adjust-sale/:invoiceId',
    component: AdjustInvoiceComponent,
    resolve: {init: AdjustInvoiceStudentResolverService},
    canDeactivate: [VatDeactivateGuard],
    data: {
      type: '02GTTT',
      brec: 'create_invoice_adjustment',
    }
  },
  {
    path: 'edit-replace-vat/:invoiceId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ReplaceInvoiceStudentResolver},
    data: {
      type: '01GTKT',
      brec: 'create_invoice_instead',
    }
  },
  {
    path: 'edit-replace-sale/:invoiceId',
    component: ReplaceInvoiceComponent,
    canDeactivate: [VatDeactivateGuard],
    resolve: {init: ReplaceInvoiceStudentResolver},
    data: {
      type: '02GTTT',
      brec: 'create_invoice_instead',
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HandleInvoiceStudentRouting {
}
