import { Injectable } from '@angular/core';
import { Router, Resolve, RouterStateSnapshot, ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { take, map } from 'rxjs/operators';

import { VatService } from '@modules/vat/shared';
import { Library, Helper, AuthService } from '@core/index';

@Injectable()
export class ReplaceInvoiceStudentResolver implements Resolve<any[]> {
  constructor(
    private vatService: VatService,
    private router: Router,
    private authService: AuthService,
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> {
    Library.showloading();
    let check = false;
    let dmHS = ',DM_TY_LE_MIEN_GIAM_HOC_PHI,DM_HINH_THUC_THU,DM_LOP_HOC';
    let origin_id = route.paramMap.get('originId');
    let invoiceId = route.paramMap.get('invoiceId');
    let loaiHoaDon = route.data.type;
    let isTT78 = false;
    let isViewTT32 = false;
    if (route.queryParams['status'] === 'viewInvTT32') {
      isViewTT32 = true;
    }
    let infoDangKy = this.authService.getInfoRegister();
    if (infoDangKy && infoDangKy.id) {
      if (infoDangKy.status == "CHAP_NHAN")
        isTT78 = true;
    }
    if (isTT78 == true && isViewTT32)
      isTT78 = false;
    if (invoiceId) {
      check = true;
    }
    if (isTT78) {
      return this.vatService.showInvoiceReplaceResolverTT78(origin_id || invoiceId, check, loaiHoaDon, dmHS).pipe(
        take(1),
        map(rs => {
          Library.hideloading();
          if (rs[0].status == "success" && rs[1].status == "success" && rs[2].status == "success" && rs[3].status == "success") {
            let templates = Helper.convertTemplatesToInvoiceResolver(rs[0]);
            return [
              templates,
              rs[1].data,
              rs[2].data.products,
              rs[3].data.configs,
              rs[4].data.invoice,
              rs[5].data.invoice_reports,
            ];
          } else { // error
            if (loaiHoaDon == "01GTKT")
              this.router.navigate(['/system/vat-invoice']);
            else
              this.router.navigate(['/system/sale-invoice']);
            return null;
          }
        })
      );
    }
    else {
      return this.vatService.showInvoiceReplaceResolver(origin_id || invoiceId, check, loaiHoaDon, dmHS).pipe(
        take(1),
        map(rs => {
          Library.hideloading();
          if (rs[0].status == "success" && rs[1].status == "success" && rs[2].status == "success" && rs[3].status == "success") {
            let invoice_releases = Helper.convertDataToInvoiceResolver(rs[0]);
            return [
              invoice_releases,
              rs[1].data,
              rs[2].data.products,
              rs[3].data.configs,
              rs[4].data.invoice,
              rs[5].data.invoice_reports,
            ];
          } else { // error
            if (loaiHoaDon == "01GTKT")
              this.router.navigate(['/system/vat-invoice']);
            else
              this.router.navigate(['/system/sale-invoice']);
            return null;
          }
        })
      );
    }
  }
}
